import React, { useState } from 'react'
import AssignmentTurnedInIcon from '@mui/icons-material/AssignmentTurnedIn'
import MiscellaneousServicesIcon from '@mui/icons-material/MiscellaneousServices'
import EditNoteIcon from '@mui/icons-material/EditNote'

const useServices = () => {
    const steps = [
      {
        title: 'Elegí el servicio que necesitás',
        description: 'Seleccioná entre actualización, reparación o armado de PC. Te guiamos en cada paso.',
        subtitle: 'Seleccioná lo que necesitás de forma simple y rápida.',
        image: 'https://picsum.photos/seed/step1/800/500',
        icon: <MiscellaneousServicesIcon sx={{ fontSize: 50, color: '#1976d2' }} />
      },
      {
        title: 'Completá los datos de tu equipo',
        description: 'Te pedimos información importante como marca, modelo y detalles técnicos opcionales.',
        subtitle: 'Necesitamos estos datos para personalizar tu servicio.',
        image: 'https://picsum.photos/seed/step2/800/500',
        icon: <EditNoteIcon sx={{ fontSize: 50, color: '#388e3c' }} />
      },
      {
        title: 'Recibí tu número de orden',
        description: 'Te brindamos un código para seguir el estado de tu equipo desde tu cuenta.',
        subtitle: 'Con tu orden podés hacer seguimiento 24/7.',
        image: 'https://picsum.photos/seed/step3/800/500',
        icon: <AssignmentTurnedInIcon sx={{ fontSize: 50, color: '#f57c00' }} />,
        testimonial: '“Usé este servicio para actualizar mi notebook y quedó como nueva 💻” – Cliente feliz'
      }
    ]

    const serviceOptions = [
      {
        id: 1,
        label: 'Actualización de Notebooks',
        image: 'https://picsum.photos/id/1015/600/400'
      },
      {
        id: 2,
        label: 'Reparación de Notebooks',
        image: 'https://picsum.photos/id/1011/600/400'
      },
      {
        id: 3,
        label: 'Armado de PC + Asesoría',
        image: 'https://picsum.photos/id/1016/600/400'
      }
    ]

    const [selectedService, setSelectedService] = useState(serviceOptions[0].id)
    const [currentStep, setCurrentStep] = useState(0)

    const handleNext = () => {
      setCurrentStep((prev) => Math.min(prev + 1, steps.length))
    }
  
    const handleBack = () => {
      setCurrentStep((prev) => Math.max(prev - 1, 0))
    }

    const state = {
      selectedService,
      currentStep,
      setSelectedService
    }
    
    const handlers = {
      handleNext,
      handleBack
    }

    const context = {
      steps,
      serviceOptions
    }
    
    return { state, handlers, context }
}

export default useServices