import React from 'react'
import { Box, Container, Typography, Grid, Button, TextField } from '@mui/material'
import { motion, AnimatePresence } from 'framer-motion'
import { Pagination } from 'swiper/modules'
import { Swiper, SwiperSlide } from 'swiper/react'
import CheckCircleIcon from '@mui/icons-material/CheckCircle'
import RadioButtonUncheckedIcon from '@mui/icons-material/RadioButtonUnchecked'
import useServices from './useServices'
import 'swiper/css/pagination'
import 'swiper/css'

const Services = () => {
  const { state, handlers, context } = useServices()

  return (
    <Box sx={{ minHeight: 'calc(100vh - 64px)', display: 'flex', alignItems: 'center', justifyContent: 'center', px: 2 }}>
      <Container maxWidth="lg">
        <AnimatePresence mode="wait">
          {state.currentStep < context.steps.length ? (
            <motion.div
              key={state.currentStep}
              initial={{ opacity: 0, x: 100 }}
              animate={{ opacity: 1, x: 0 }}
              exit={{ opacity: 0, x: -100 }}
              transition={{ duration: 0.5 }}>

              <Grid container spacing={4} alignItems="center" direction={{ xs: 'column', md: state.currentStep % 2 === 0 ? 'row' : 'row-reverse' }}>
                <Grid item xs={12} md={6}>
                  <Box
                    component="img"
                    src={context.steps[state.currentStep].image}
                    alt={context.steps[state.currentStep].title}
                    sx={{
                      width: '100%',
                      borderRadius: 2,
                      height: { xs: 'auto', md: 400 },
                      objectFit: 'cover',
                    }}
                  />
                </Grid>
                <Grid item xs={12} md={6}>
                  <Box sx={{ display: 'flex', alignItems: 'center', mb: 1 }}>
                    <Typography variant="overline" sx={{ fontSize: '1rem', fontWeight: 'bold', letterSpacing: 2, color: 'gray', textTransform: 'uppercase', mr: 2 }}>
                      Paso {state.currentStep + 1}
                    </Typography>
                    {context.steps[state.currentStep].icon}
                  </Box>
                  <Typography variant="h4" gutterBottom sx={{ fontSize: { xs: '1.5rem', md: '2.5rem' } }}>{context.steps[state.currentStep].title}</Typography>
                  <Typography variant="body1" color="text.secondary" sx={{ fontSize: { xs: '1rem', md: '1.25rem' } }}>{context.steps[state.currentStep].description}</Typography>
                  <Typography variant="body2" color="text.secondary" sx={{ fontSize: { xs: '0.9rem', md: '1.1rem' } }}>{context.steps[state.currentStep].subtitle}</Typography>
                  {context.steps[state.currentStep].testimonial && (<Typography variant="body2" color="text.secondary" fontStyle="italic" mt={2}>{context.steps[state.currentStep].testimonial}</Typography>)}
                  <Box sx={{ mt: 4, display: 'flex', gap: 2, justifyContent: { xs: 'center', md: 'start' } }}>
                    {state.currentStep > 0 && <Button variant="outlined" onClick={handlers.handleBack}>Anterior</Button>}
                    <Button variant="contained" onClick={handlers.handleNext}>Siguiente</Button>
                  </Box>
                </Grid>
              </Grid>
            </motion.div>
          ) : (
            <Box py={4}>
              <motion.div key="form"
                initial={{ opacity: 0, x: 100 }}
                animate={{ opacity: 1, x: 0 }}
                exit={{ opacity: 0, x: -100 }}
                transition={{ duration: 0.5 }}>

                <Box sx={{ position: 'relative', pb: 6 }}>
                  <Swiper spaceBetween={20}
                    slidesPerView={1}
                    pagination={{ clickable: true, el: '.swiper-custom-pagination' }}
                    breakpoints={{
                      600: { slidesPerView: 1 },
                      900: { slidesPerView: 2 },
                      1200: { slidesPerView: 3 }
                    }}
                    modules={[Pagination]}>

                    {context.serviceOptions.map((option) => (
                      <SwiperSlide key={option.id}>
                        <Box
                          onClick={() => state.setSelectedService(option.id)}
                          sx={{
                            position: 'relative',
                            cursor: 'pointer',
                            borderRadius: 3,
                            overflow: 'hidden',
                            transition: 'all 0.3s ease-in-out',
                          }}>

                          <Box sx={{ position: 'absolute', top: 8, left: 8, zIndex: 2 }}>
                            {state.selectedService === option.id ? (
                              <CheckCircleIcon sx={{ color: '#1976d2', fontSize: 28 }} /> ) : (
                              <RadioButtonUncheckedIcon sx={{ color: 'white', fontSize: 28 }} />
                            )}
                          </Box>
                          <Box component="img" src={option.image} alt={option.label}
                            sx={{
                              width: '100%',
                              height: '220px',
                              objectFit: 'cover',
                              display: 'block'
                            }} />

                          <Box
                            sx={{
                              position: 'absolute',
                              bottom: 0,
                              width: '100%',
                              bgcolor: 'rgba(0,0,0,0.6)',
                              color: 'white',
                              py: 2,
                              textAlign: 'center',
                            }}>

                            <Typography variant="h6" sx={{ fontSize: { xs: '1rem', sm: '1.4rem' }, fontWeight: 'bold', px: 1 }}>{option.label}</Typography>
                          </Box>
                        </Box>
                      </SwiperSlide>
                    ))}
                  </Swiper>
                  <Box className="swiper-custom-pagination" sx={{ mt: 2, textAlign: 'center' }}></Box>
                </Box>

                <Typography variant="h5" align="center" gutterBottom>Completá los datos para solicitar el servicio</Typography>

                <Box component="form" sx={{ display: 'flex', flexDirection: 'column', gap: 2 }}>
                  <TextField label="Marca" required fullWidth />
                  <TextField label="Modelo" required fullWidth />
                  {state.selectedService === 1 && (
                    <Box>
                      <TextField label="Componentes a actualizar (opcional)" fullWidth />
                      <TextField label="Sistema operativo preferido" fullWidth />
                    </Box>
                  )}
                  <TextField label="Número de serie (opcional)" fullWidth />
                  <TextField label="Comentarios adicionales (opcional)" fullWidth multiline minRows={3} />
                  <Box sx={{ display: 'flex', justifyContent: 'center', gap: 2, mt: 2 }}>
                    <Button variant="outlined" onClick={handlers.handleBack}>Anterior</Button>
                    <Button variant="contained">Enviar solicitud</Button>
                  </Box>
                </Box>
              </motion.div>
            </Box>
          )}
        </AnimatePresence>
      </Container>
    </Box>
  )
}

export default Services
